import Page from "./page";
import storage from './storage';
import Category from "./categories";
import { onEnterDo, arrayEqual } from "./util";
import PageRoute from "./page-route";
import router from './router';
import * as ons from 'onsenui';


class AddCategoryPage extends Page {
    constructor(pageElement) {
        super(pageElement);

        this.addCategoryPageItemsToAdd = document.getElementById('add-category-page-items-to-add');
        this.addCategoryToolbarButton = document.getElementById('add-category-toolbar-button');
        this.addCategoryButton = document.getElementById('add-category-button');
        this.addCategoryTitleInput = document.getElementById('add-category-title');

        this.path = pageElement.data.path || [];
        this.selectedFeeds = {};
        this.selectedCategories = {};

        this.init();
    }

    init = async () => {
        this.feeds = await storage.getFeeds();
        this.root = Category.fromFeedsObj(this.feeds);
        this.parentCategory = this.root.getScopedCategories(this.path.slice(0, -1));

        this.render();

        this.addCategoryButton.addEventListener('click', this.done);
        this.addCategoryToolbarButton.addEventListener('click', this.done);
        onEnterDo(this.addCategoryTitleInput, this.done);
    }

    done = async () => {
        var value = this.addCategoryTitleInput.value;
        this.addCategoryTitleInput.blur();
        if (value.length < 2) {
            ons.notification.alert('Name of a category must be more then 1 character.', { cancelable: true });
        } else if (value.length > 50) {
            ons.notification.alert('Name of a category can not be more then 50 characters.', { cancelable: true })
        } else if (value in this.parentCategory.categories) {
            ons.notification.alert('This category name already exists.', { cancelable: true });
        } else if (Object.keys(this.selectedFeeds).length === 0
            && Object.keys(this.selectedCategories).length === 0) {
            ons.notification.alert('The category must contain at least one feed or sub category.', { cancelable: true });
        } else {
            var finalCategoryTitle = this.path.concat(value);

            for (var concatnatedCategoryPath in this.selectedCategories) {
                var newSubCategory = this.selectedCategories[concatnatedCategoryPath];
                var title = newSubCategory.title;
                // var safeTitle = title;
                // var count = 1;
                // while (safeTitle in newSubCategory.categories) {
                //     safeTitle = title + ' (' + (count++) + ')';
                // }
                try {
                    newSubCategory.moveTo(finalCategoryTitle.concat(title));
                } catch (err) {
                    ons.notification.alert('Something went wrong while moving categories around. '
                        + 'Some of you categories might be copied and some might not.', { cancelable: true });
                    console.error(err);
                }
            }
            this.feeds = await storage.getFeeds();
            var ret = [];
            for (var url in this.selectedFeeds) {
                var feed = this.feeds[url];
                feed.category = finalCategoryTitle;
                ret.push(storage.putFeed(feed));
            }
            await Promise.all(ret);

            var event = new CustomEvent("categorieschanged", {
                detail: {  }
            });
            document.dispatchEvent(event);

            router.navigator.replacePage('category.html', {
                data: {
                    category: finalCategoryTitle,
                }
            });
        }
    }

    reload = async () => {
        this.parentCategory = await Category.promiseFromFeedStorage();
        this.render();
    }

    render = () => {
        this.recursiveCategoriesRendering(this.parentCategory, this.addCategoryPageItemsToAdd, true);
    }

    recursiveCategoriesRendering(parentCategory, parentList, isRoot) {
        var nestedOnsList = isRoot
            ? document.createDocumentFragment()
            : this.renderCategory(parentCategory, parentList);
        var empty = true;
        for (let key in parentCategory.categories) {
            var cat = parentCategory.categories[key];
            this.recursiveCategoriesRendering(cat, nestedOnsList, false);
            empty = false;
        }
        for (let url in parentCategory.feeds) {
            var feed = this.feeds[url];
            this.renderFeedItem(feed, nestedOnsList);
            empty = false;
        }
        if (empty) {
            this.renderEmptyItem(nestedOnsList);
        }
        if (isRoot) {
            this.addCategoryPageItemsToAdd.appendChild(nestedOnsList);
        }
    }

    renderCategory = (subCategory, parentList) => {
        var listItem = document.createElement('ons-list-item');
        listItem.setAttribute('tappable', true);
        listItem.setAttribute('expandable', true);

        var expandableContent = document.createElement('div');
        expandableContent.setAttribute('class', 'expandable-content');
        listItem.appendChild(expandableContent);

        var nestedOnsList = document.createElement('ons-list');
        expandableContent.appendChild(nestedOnsList);

        var left = document.createElement('div');
        left.setAttribute('class', 'left');
        listItem.appendChild(left);

        var onsIcon = document.createElement('ons-icon');
        onsIcon.setAttribute('icon', 'md-collection-item-2');
        left.appendChild(onsIcon);

        var center = document.createElement('div');
        center.setAttribute('class', 'center');
        center.innerText = subCategory.title;
        listItem.appendChild(center);

        var right = document.createElement('div');
        right.setAttribute('class', 'right');
        listItem.appendChild(right);

        var onsSwitch = document.createElement('ons-switch');

        var catPath = subCategory.getCategoryPath();

        var isParent = Category.checkIfCategoryIsChild(catPath, this.path)
        if (isParent) {
            onsSwitch.setAttribute('disabled', true);
        }

        var isDirectChild = subCategory in this.parentCategory.categories;
        if (isDirectChild) {
            onsSwitch.checked = true;
            onsSwitch.setAttribute('disabled', true);
        }

        var isEqual = arrayEqual(this.path, subCategory.getCategoryPath());
        if (isEqual) {
            onsSwitch.setAttribute('disabled', true);
        }

        onsSwitch.addEventListener('change', (e) => {
            this.selectedCategories[catPath.join('\n')] = subCategory;
        });

        onsSwitch.addEventListener('click', function (e) {
            e.stopPropagation();
        });

        right.appendChild(onsSwitch);

        parentList.appendChild(listItem);
        return nestedOnsList;
    }

    renderFeedItem = (feed, parentList) => {
        var listItem = document.createElement('ons-list-item');

        var left = document.createElement('div');
        left.setAttribute('class', 'left');
        listItem.appendChild(left);

        var onsIcon = document.createElement('ons-icon');
        onsIcon.setAttribute('icon', 'md-collection-item-2');
        left.appendChild(onsIcon);

        var center = document.createElement('div');
        center.setAttribute('class', 'center');
        listItem.appendChild(center);

        var title = document.createElement('span');
        title.setAttribute('class', 'list-item__title overflow-ellipsis');
        title.innerText = feed.title;
        center.appendChild(title);

        var right = document.createElement('div');
        right.setAttribute('class', 'right');
        listItem.appendChild(right);

        var onsSwitch = document.createElement('ons-switch');
        right.appendChild(onsSwitch);

        // var isDirectChild = feed.url in category.feeds;
        // if (isDirectChild) {
        //     onsSwitch.checked = true;
        //     onsSwitch.setAttribute('disabled', true);
        // }

        onsSwitch.addEventListener('change', (e) => {
            this.selectedFeeds[feed.url] = e.checked;
        });

        parentList.appendChild(listItem);
    }

    renderEmptyItem = (nestedOnsList) => {

    }
}

router.addPage(new PageRoute(
    'add-category.html',
    'add-category-page',
    pageElement => new AddCategoryPage(pageElement)));

export default AddCategoryPage;

