import PageRoute from "./page-route";
import router from "./router";
import Page from "./page";
import iconnotfoundimage from './images/iconnotfound.png';
import storage from "./storage";
import { excerpt, logError } from "./util";
import { format } from 'timeago.js';
import URL from './urls';
import createDOMPurify from 'dompurify';
import { setBackgroundAudio, getBackgroundAudio } from "./background-audio";
import { share } from './sharing';
import bookmarks from "./bookmarks";

const DOMPurify = createDOMPurify(window);

class SpecificNewsItemPage extends Page {
    constructor(pageElement) {
        super(pageElement);


        let specificNewsItemPage = document.getElementById('specific-news-item-page');
        let specificNewsItemPageToolbarTitle = document.getElementById('specific-news-item-page-toolbar-title');
        let specificNewsItemPageFeedTitle = document.getElementById('specific-news-item-page-feed-title');
        let specificNewsItemPageIcon = document.getElementById('specific-news-item-page-icon');
        let specificNewsItemPageTitle = document.getElementById('specific-news-item-page-title');
        let specificNewsItemPageDescription = document.getElementById('specific-news-item-page-description');
        let specificNewsItemPageLinkFab = document.getElementById('specific-news-item-page-link-fab');
        let specificNewsItemPageContent = document.getElementById('specific-news-item-page-content');
        let specificNewsItemPageAuthor = document.getElementById('specific-news-item-page-author');
        let specificNewsItemPageTime = document.getElementById('specific-news-item-page-time');
        let specificNewsItemPageContentLink = document.getElementById('specific-news-item-page-content-link');
        let specificNewsItemPageEnclosures = document.getElementById('specific-news-item-page-enclosures');
        this.specificNewsItemPageToolbarRight = pageElement.querySelector('.toolbar .right');
        this.specificNewsItemPageToolbarBookmark = document.getElementById('specific-news-item-page-toolbar-bookmark');

        let item = this.item = pageElement.data.item;
        let feed = this.feed = pageElement.data.feed;

        specificNewsItemPageToolbarTitle.innerText = excerpt(item.title, 100) || 'News Item';
        specificNewsItemPageFeedTitle.innerText = excerpt(feed.title, 200);
        specificNewsItemPageTitle.textContent = excerpt(item.title, 500) || 'News Item';
        specificNewsItemPageIcon.addEventListener('error', fn.handleerroriconnotfound);
        specificNewsItemPageIcon.setAttribute('src', feed.icon || iconnotfoundimage);

        this.initSharing();
        this.initBookmarks();

        if (item.image) {
            let enclosureListItem = document.createElement('ons-list-item');
            let center = document.createElement('div');
            center.setAttribute('class', 'center');
            enclosureListItem.appendChild(center);
            let innerCenter = document.createElement('div');
            center.appendChild(innerCenter);
            let enclosureImg = document.createElement('img');
            enclosureImg.setAttribute('class', 'enclosure-item');
            enclosureImg.setAttribute('src', item.image.url);
            enclosureImg.alt = excerpt(item.description, 150)
            enclosureImg.addEventListener('error', fn.handlenotfound);
            innerCenter.appendChild(enclosureImg);
            specificNewsItemPageEnclosures.appendChild(enclosureListItem);
        }
        if (item.video) {
            let enclosureListItem = document.createElement('ons-list-item');
            let center = document.createElement('div');
            center.setAttribute('class', 'center');
            enclosureListItem.appendChild(center);
            let innerCenter = document.createElement('div');
            center.appendChild(innerCenter);
            let enclosureVideo = document.createElement('video');
            enclosureVideo.setAttribute('controls', true);
            enclosureVideo.setAttribute('class', 'enclosure-item');
            enclosureVideo.setAttribute('src', item.video.url);
            enclosureVideo.setAttribute('type', item.video.type);
            enclosureVideo.addEventListener('error', function (error) {
                showEnclosureError(enclosureVideo, 'Unable to play video 😢');
            });
            enclosureVideo.appendChild(document.createTextNode(
                'Your browser does not support "video" elements.'));
            innerCenter.appendChild(enclosureVideo);
            specificNewsItemPageEnclosures.appendChild(enclosureListItem);
        }
        if (item.audio) {
            let enclosureListItem = document.createElement('ons-list-item');
            let center = document.createElement('div');
            center.setAttribute('class', 'center');
            enclosureListItem.appendChild(center);
            let innerCenter = document.createElement('div');
            innerCenter.setAttribute('style', 'width: 100%');
            center.appendChild(innerCenter);
            let enclosureAudio = document.createElement('audio');
            enclosureAudio.setAttribute('controls', true);
            enclosureAudio.setAttribute('class', 'enclosure-item');
            enclosureAudio.setAttribute('src', item.audio.url);
            enclosureAudio.setAttribute('type', item.audio.type);
            enclosureAudio.addEventListener('error', function (error) {
                showEnclosureError(enclosureAudio, 'Unable to play audio 😢');
            });
            let playInBackgroundButton = document.createElement('ons-button');
            playInBackgroundButton.innerText = 'Play in the background';
            playInBackgroundButton.setAttribute('style', 'float:right;margin-top: 5px;');
            playInBackgroundButton.addEventListener('click', async e => {
                enclosureListItem.style.height = enclosureListItem.offsetHeight + 'px';
                enclosureListItem.classList.add('close');
                await setBackgroundAudio(enclosureAudio);
                if (enclosureAudio.paused) enclosureAudio.play();
            });
            enclosureAudio.addEventListener('play', () => {
                var bgAudio = getBackgroundAudio();
                if (bgAudio && bgAudio !== enclosureAudio) {
                    bgAudio.pause();
                }
            });
            enclosureAudio.appendChild(document.createTextNode(
                'Your browser does not support "audio" elements.'));
                innerCenter.appendChild(enclosureAudio);
                innerCenter.appendChild(playInBackgroundButton);
            specificNewsItemPageEnclosures.appendChild(enclosureListItem);
        }
        if (item.summary) {
            let dirtyDescription = excerpt(item.summary, 300000) || '';
            specificNewsItemPageDescription.innerHTML = DOMPurify.sanitize(dirtyDescription, {
                ALLOWED_TAGS: ['h1', 'h2', 'h3', 'h4', 'h5', 'h6', 'blockquote', 'p', 'a', 'ul', 'ol',
                    'nl', 'li', 'b', 'i', 'strong', 'em', 'strike', 'code', 'hr', 'br', 'div',
                    'table', 'thead', 'caption', 'tbody', 'tr', 'th', 'td', 'pre', /*'iframe',*/
                    'img'],
                ADD_ATTR: ['src', 'href', 'name', 'target']
            });
        }
        else if (item.description && !item.descIsExcerpt) {
            specificNewsItemPageDescription.innerText = excerpt(item.description, 2000) || '';
        } else {
            specificNewsItemPageDescription.style.display = 'none';
        }
        specificNewsItemPageTime.innerText = format(item.date) + ' - ' + new Date(item.date).toLocaleString();
        specificNewsItemPageAuthor.innerText = item.author;
        specificNewsItemPageLinkFab.addEventListener('click', function () {
            window.open(item.link, '_system');
        });
        if (item.content) {
            let baseUrl = item.contentBase || feed.site;
            let dirty = excerpt(item.content, 1000000);
            specificNewsItemPageContent.innerHTML = DOMPurify.sanitize(dirty, {
                ALLOWED_TAGS: ['h1', 'h2', 'h3', 'h4', 'h5', 'h6', 'blockquote', 'p', 'a', 'ul', 'ol',
                    'nl', 'li', 'b', 'i', 'strong', 'em', 'strike', 'code', 'hr', 'br', 'div',
                    'table', 'thead', 'caption', 'tbody', 'tr', 'th', 'td', 'pre', /*'iframe',*/
                    'img'],
                ADD_ATTR: ['src', 'href', 'name', 'target']
            });
            specificNewsItemPageContent.querySelectorAll('[href]')
                .forEach(function (hrefAttrElem) {
                    hrefAttrElem.href = new URL(baseUrl).normalizeUrl(hrefAttrElem.getAttribute('href'));
                });
            specificNewsItemPageContent.querySelectorAll('[src]')
                .forEach(function (srcAttrElem) {
                    srcAttrElem.setAttribute('src', new URL(baseUrl).normalizeUrl(srcAttrElem.getAttribute('src')).string);
                });
            specificNewsItemPageContent.querySelectorAll('a').forEach(function (anchor) {
                anchor.target = '_system';
            });
            specificNewsItemPageContent.querySelectorAll('img')
                .forEach(function (image) {
                    image.addEventListener('error', fn.handlenotfound);
                });
            specificNewsItemPageDescription.querySelectorAll('img')
                .forEach(function (image) {
                    image.addEventListener('error', fn.handlenotfound);
                });
        } else if (item.link) {
            specificNewsItemPageContentLink.style.display = 'block';
            specificNewsItemPageContentLink.href = item.link;
        }

        function showEnclosureError(belowElem, msg) {
            let paragraph = document.createElement('p');
            let span = document.createElement('span');
            paragraph.appendChild(span);
            paragraph.setAttribute('class', 'enclosure-error-msg');
            let icon = document.createElement('ons-icon');
            icon.setAttribute('icon', 'md-alert-triangle');
            span.appendChild(icon);
            span.appendChild(document.createTextNode(msg));
            belowElem.appendChild(paragraph);
        }
    }

    share = () => {
        share(
            this.item.title,
            this.item.description,
            this.item.link);
    }

    initSharing = () => {
        var toolbarBtn = document.createElement("ons-toolbar-button");
        toolbarBtn.addEventListener('click', this.share);
        toolbarBtn.setAttribute('class', '');
        var icon = document.createElement("ons-icon");
        icon.setAttribute("icon", "md-share");
        toolbarBtn.appendChild(icon);
        this.specificNewsItemPageToolbarRight.appendChild(toolbarBtn);
    }

    initBookmarks = async () => {
        const list = await bookmarks.getItems();
        let isBookMarked = !!list.find(bookmark => bookmark.item.link === this.item.link);
        if (isBookMarked) {
            this.specificNewsItemPageToolbarBookmark
                .querySelector('.ons-icon')
                .setAttribute('icon', 'md-bookmark');
        }

        this.specificNewsItemPageToolbarBookmark.addEventListener('click', e => {
            if (isBookMarked) {
                isBookMarked = false;
                this.specificNewsItemPageToolbarBookmark
                    .querySelector('.ons-icon')
                    .setAttribute('icon', 'md-bookmark-outline');
                bookmarks.removeItem(this.item);
            } else {
                isBookMarked = true;
                this.specificNewsItemPageToolbarBookmark
                    .querySelector('.ons-icon')
                    .setAttribute('icon', 'md-bookmark');
                bookmarks.addItem(this.item, this.feed);
            }
        });
    }

}

router.addPage(new PageRoute(
    'specific-news-item.html',
    'specific-news-item-page',
    pageElement => new SpecificNewsItemPage(pageElement)
));

export default SpecificNewsItemPage;
