import { strRepeat, quoteattr, getContent, getContentAttr } from "./util";
import { fetchXML } from "./feed-helpers";

export const parseOPMLFile = file => {
    return new Promise((resolve, reject) => {
        let fr = new FileReader();
        fr.onload = function () {
            var doc = (new DOMParser()).parseFromString(fr.result, "text/xml");
            return resolve(parseOPMLXML(doc));
        };
        fr.onerror = reject;
        fr.readAsText(file);
    });
}

export const parseOPMLXML = (xmlDocument) => {
    var outline = [].slice.apply(xmlDocument.getElementsByTagName('outline'))
        .map(function (tag) {
            if (tag.hasAttribute('xmlUrl') === false) return null;
            var categories = [];
            var t = tag;
            while (t.parentElement && t.parentElement.tagName.toUpperCase() === 'OUTLINE') {
                t = t.parentElement;
                // categories.push({
                //     title: t.getAttribute('title'),
                //     text: t.getAttribute('text'),
                // });
                categories.push(t.getAttribute('title'));
            }
            return {
                xmlUrl: tag.getAttribute('xmlUrl'),
                type: tag.getAttribute('type'),
                title: tag.getAttribute('title'),
                text: tag.getAttribute('text'),
                categories: categories
            }
        }).filter(function (item) {
            return item !== null;
        });

    return {
        originalXMLDocument: xmlDocument,
        title: getContent(xmlDocument, 'title'),
        dateCreated: getContent(xmlDocument, 'dateCreated'),
        dateModified: getContent(xmlDocument, 'dateModified'),
        outline: outline
    };
}

export const parseOPMLURL = async url => {
    const { responseText, responseXML } = await fetchXML(url);
    return parseOPMLXML(responseXML);
}

export const getOPMLXML = (feeds, category) => {
    var contents = '<?xml version="1.0" encoding="UTF-8"?>\n\n'
        + '<opml version="1.0">\n'
        + '\t<head>\n\t<title>Newfeeder subscriptions</title>\n'
        + '\t</head>\n'
        + '\t<body>\n';

    getOPMLRecursive(category, 0);


    contents += '\t</body>\n';
    contents += '</opml>';

    return contents;

    function getOPMLRecursive(categories, indentation) {
        for (var url in categories.feeds) {
            var feed = feeds[url];
            var type = feed.type.toLowerCase();
            var text = feed.description;
            var title = feed.title;
            var htmlUrl = feed.site;
            var xmlUrl = url;

            contents += strRepeat('\t', indentation)
                + '\t\t<outline type="' + type + '" ';
            if (text) contents += 'text="' + quoteattr(text) + '" ';
            contents += 'title="' + quoteattr(title) + '" ';
            contents += 'xmlUrl="' + quoteattr(xmlUrl) + '" ';
            if (htmlUrl) contents += 'htmlUrl="' + quoteattr(htmlUrl) + '" />\n';
        }
        for (var title in categories.categories) {
            var subCategories = categories.categories[title];
            contents += strRepeat('\t', indentation)
                + '\t\t<outline title="' + quoteattr(title) + '">\n';
            getOPMLRecursive(subCategories, indentation + 1);
            contents += strRepeat('\t', indentation)
                + '\t\t</outline>\n';
        }
    }
}
