import storage from "./storage";
import { localization, userLang } from './localization';
import { dataURItoBlob, download } from './util';

const isFirstTimeAppOpening = storage.constructor.isFirstTimeAppOpening;

function isOnline () {

}

function isOffline () {
    showNoConnectionToast();
};

if (window.addEventListener) {
    window.addEventListener("online", isOnline, false);
    window.addEventListener("offline", isOffline, false);
}
else {
    document.body.ononline = isOnline;
    document.body.onoffline = isOffline;
}

document.addEventListener("resume", function () {
    if (window.navigator.onLine === false) {
        showNoConnectionToast();
    }
}, false);

ons.ready(function () {
    if (window.cordova && cordova.platformId === 'browser') {
        require('./browser');
    } else {
        require('./admob');
    }
    if (navigator.onLine === false) {
        showNoConnectionToast();
    }
});

ons.disableIconAutoPrefix()

ons.platform.select('android');
ons.disableAutoStatusBarFill();

function showNoConnectionToast() {
    var message = localization.noConnection[userLang];
    ons.notification.toast(message, {
        timeout: 5000,
        animation: 'ascend'
    });
}



