import Page from "./page";
import router from "./router";
import PageRoute from "./page-route";
import { feedRecommandations, recommandedFeedSources, userLang } from "./localization";
import storage from "./storage";
import { excerpt } from "./util";
import { feedsRefetch, fetchFeed } from "./feed-helpers";

class RecommendedFeedsPage extends Page {
    constructor(pageElement) {
        super(pageElement);

        var recommandedFeeds = feedRecommandations[userLang];
        var recommandedSites = recommandedFeedSources[userLang];

        var recommendedFeedsItems = document.getElementById('recommended-feeds-items');
        var recommendedSitesItems = document.getElementById('recommended-sites-items');

        storage.getFeeds().then(function (persistFeeds) {
            pageElement.addEventListener('hide', feedsRefetch);

            const feedsFrag = document.createDocumentFragment();
            for (let i = 0; i < recommandedFeeds.length; i++) {
                (function (feedInfo) {
                    var siteUrlParts = feedInfo.site.split('/');
                    var icon = siteUrlParts[0].replace('http:', 'https:') + '//' + siteUrlParts[2] + '/favicon.ico';
                    var checked = !!persistFeeds[feedInfo.url];

                    var div = document.createElement('div');

                    div.innerHTML = '<ons-list-item>\
                    <div class="left">\
                    <img onerror="fn.handleerroriconnotfound.call(this)" src="' + icon + '" class="list-item__thumbnail">\
                    </div>\
                    <div class="center">\
                    <span class="list-item__title overflow-ellipsis">' + feedInfo.title + '</span>\
                    <span class="list-item__subtitle overflow-ellipsis">' + feedInfo.site + '<span>\
                    </div>\
                    <div class="right"><ons-switch ' + (checked ? 'checked' : '') + ' class="recommended-feed-switch"></ons-switch></div>\
                    </ons-list-item>';

                    var elem = div.firstChild;
                    var onsSwitch = elem.getElementsByClassName('recommended-feed-switch')[0];
                    onsSwitch.addEventListener('change', async e => {
                        const newFeeds = await storage.getFeeds();
                        persistFeeds = newFeeds;
                        if (e.target.checked && !persistFeeds[feedInfo.url]) {
                            const feed = await fetchFeed(feedInfo.url);
                            storage.putFeed(feed);
                        } else if (!e.target.checked && persistFeeds[feedInfo.url]) {
                            storage.deleteFeed(feedInfo);
                        }
                    });
                    feedsFrag.appendChild(elem);
                })(recommandedFeeds[i]);
            }
            recommendedFeedsItems.appendChild(feedsFrag);

            const siteFrags = document.createDocumentFragment();
            for (let i = 0; i < recommandedSites.length; i++) {
                (function (site) {
                    var siteUrlParts = site.url.split('/');
                    var icon = siteUrlParts[0].replace('http:', 'https:') + '//' + siteUrlParts[2] + '/favicon.ico';

                    var div = document.createElement('div');

                    div.innerHTML = '<ons-list-item tappable \
                    onclick="window.open(\'' + site.url + '\', \'_system\')" modifier="chevron">\
                    <div class="left">\
                    <img onerror="fn.handleerroriconnotfound.call(this)" src="' + icon + '" class="list-item__thumbnail">\
                    </div>\
                    <div class="center">' + site.title + '</div>\
                    </ons-list-item>';

                    var elem = div.firstChild;
                    siteFrags.appendChild(elem);
                })(recommandedSites[i]);
            }
            recommendedSitesItems.appendChild(siteFrags);
        });
    }
}

router.addPage(new PageRoute(
    'recommended.html',
    'recommended-page',
    pageElement => new RecommendedFeedsPage(pageElement)
));

export default RecommendedFeedsPage;