import router from './router';
import { debounce, escapeRegExp, excerpt } from './util';
import PageRoute from './page-route';
import storage from './storage';
import { feedsRefetch } from './feed-helpers';
import { format } from 'timeago.js';
import Page from './page';

const maxLazyListItems = 3000;

function sortByDate(a, b) {
    return b.item.date - a.item.date;
}

class NewsListPage extends Page {
    constructor(pageElement) {
        super(pageElement);

        this.addFeedBtn = document.getElementById('add-feed-btn');
        this.feedList = document.getElementById('news-list');
        this.newsSearchInput = document.getElementById('search-feeds-news-list-items');
        this.newsPageProgress = document.getElementById('news-page-progress');
        this.scrollToTopFab = document.getElementById('news-page-scroll-to-top');
        this.pageContent = pageElement.querySelector('.page__content');
        this.itemTemplate = this.createTemplate();
        this.pageElement = pageElement;

        this.dockedToTop = true;
        this.pageContent.addEventListener('scroll', e => {
            if (e.target.scrollTop > 1 && this.dockedToTop === true) {
                this.dockedToTop = false;
                this.scrollToTopFab.classList.remove('hidden');
            } else if (e.target.scrollTop < 1 && this.dockedToTop === false) {
                this.dockedToTop = true;
                this.scrollToTopFab.classList.add('hidden');
            }
        });

        this.scrollToTopFab.addEventListener('click', () => {
            this.pageContent.scrollTo(0, 0);
        });

        this.addFeedBtn.addEventListener('click', e => {
            router.navigator.pushPage('add-feed-tabbar.html');
        });

        storage.getFeeds().then(this.initWithFeeds);
    }

    initWithFeeds = feeds => {
        this.feeds = feeds;
        this.newsSearchInput.addEventListener('input', () => {
            var term = this.newsSearchInput.value.trim();
            if (term.length > 2) {
                storage.getFeeds().then((newFeeds) => {
                    this.feeds = this.feeds = newFeeds;
                });
                this.renderDebounced();
            } else {
                storage.getFeeds().then((newFeeds) => {
                    this.feeds = this.feeds = newFeeds;
                });
                this.renderDebounced();
            }
        });

        this.faviconUrls = {};
        this.setFaviconURLS();
        this.ultimateList = [];

        this.feedList.delegate = {
            createItemContent: (i) => {
                return this.createListItem(this.ultimateList[i].feed, this.ultimateList[i].item);
            },
            countItems: () => {
                return Math.min(maxLazyListItems, this.ultimateList.length);
            },
            calculateItemHeight: (index) => 108.8,
        };

        this.renderDebounced = debounce(this.render, 300);
        window.addEventListener('orientationchange', this.renderDebounced);
        window.addEventListener('resize', this.renderDebounced);

        this.feedsListItems = {};
        for (var url in this.feeds) {
            var feed = this.feeds[url];
            var items = {};
            for (var j = 0; j < feed.items.length; j++) {
                items[feed.items[j].link] = {
                    item: feed.items[j],
                    feed: feed
                };
            }
            this.feedsListItems[url] = items;
        }

        setTimeout(this.render, 0);
        this.interval = setInterval(this.feedsRefetchInterval, 180 * 1000);

        document.addEventListener('feedsrefetch', this.handleFeedsRefetch);
        document.addEventListener('feedsrefetchprogress', this.handleFeedsRefetchProgress);
        document.addEventListener('resume', feedsRefetch);
        window.addEventListener('focus', feedsRefetch);

        this.pageElement.addEventListener('destroy', () => {
            document.removeEventListener('feedsrefetch', this.handleFeedsRefetch);
            document.removeEventListener('feedsrefetchprogress', this.handleFeedsRefetchProgress);
            document.removeEventListener('resume', feedsRefetch);
            window.removeEventListener('focus', feedsRefetch);
            clearInterval(this.interval);
        });
    }

    feedsRefetchInterval = () => {
        if (fn.hasFocus) feedsRefetch();
    }

    handleFeedsRefetch = () => {
        storage.getFeeds().then(newFeeds => {
            this.feeds = newFeeds;
            this.setFaviconURLS();
            var feedsArr = [];
            for (var url in this.feeds) feedsArr.push(this.feeds[url]);

            for (var i = 0; i < feedsArr.length; i++) {
                var feed = feedsArr[i];
                var url = feed.url;
                var items = {};
                var oldFeedListItems = this.feedsListItems[url];

                if (oldFeedListItems) {
                    for (var j = 0; j < feed.items.length; j++) {
                        var newItem = feed.items[j];
                        items[newItem.link] = (oldFeedListItems[newItem.link] && oldFeedListItems[newItem.link].item.should_update_token === newItem.should_update_token)
                            ? oldFeedListItems[newItem.link]
                            : {
                                item: newItem,
                                feed: feed
                            };
                    }
                } else {
                    for (var j = 0; j < feed.items.length; j++) {
                        var newItem = feed.items[j];
                        items[newItem.link] = {
                            item: newItem,
                            feed: feed
                        };
                    }
                }
                this.feedsListItems[url] = items;
            }
            setTimeout(this.render, 0);
        })
    }

    handleFeedsRefetchProgress = (e) => {
        this.newsPageProgress.value = e.detail.progress;
    }

    createListItem = (feed, item) => {
        this.itemTemplateThumbnail.setAttribute('src', this.faviconUrls[feed.url]);
        this.itemTemplateListItemTitle.innerText = excerpt(item.title, 300);
        this.itemTemplateSubtitleDescription.textContent = excerpt(item.description, 150);
        this.itemTemplateSubTitleTimeago.innerText = format(item.date);
        this.itemTemplateSubtitleFeedTitle.innerText = feed.title;
        var listItem = this.itemTemplate.cloneNode(true);
        listItem.addEventListener('click', (e) => {
            router.navigator.pushPage('specific-news-item.html', { data: { feed: feed, item: item } });
        });
        return listItem;
    }

    render = () => {
        this.setFaviconURLS();
        this.ultimateList = [];
        var c = 0;

        var term = this.newsSearchInput.value.trim();
        var shouldFilterByName = term.length > 2;
        var regex = new RegExp('.*?' + escapeRegExp(term) + '.*', 'im');

        (() => {
            for (var url in this.feedsListItems) {
                var feedItems = this.feedsListItems[url];
                var feed = this.feeds[url];
                if (!feed) continue;

                var searchMatchesFeed = regex.test(feed.title) || regex.test(feed.description);

                for (var link in feedItems) {
                    var entry = feedItems[link];
                    if (shouldFilterByName
                        && !searchMatchesFeed
                        && !regex.test(entry.item.title)
                        && !regex.test(entry.item.description))
                        continue;

                    this.ultimateList.push(entry);
                    if (c++ > maxLazyListItems) return;
                }
            }
        })();

        this.ultimateList.sort(sortByDate);

        this.feedList.refresh();
    }
    setFaviconURLS = () => {
        for (var url in this.feeds) {
            var feed = this.feeds[url];
            var urlSplit = feed.site.split('/');
            this.faviconUrls[url] = 'https://' + urlSplit[2] + '/favicon.ico';
        }
    }

    createTemplate = () => {
        // START Build list item template
        this.itemTemplate = document.createElement("ons-list-item");
        this.itemTemplate.setAttribute("tappable", "");
        this.itemTemplate.setAttribute("modifier", "chevron");
        var left = document.createElement("div");
        left.setAttribute("class", "left");
        this.itemTemplateThumbnail = document.createElement("img");
        this.itemTemplateThumbnail.setAttribute("class", "list-item__thumbnail");
        this.itemTemplateThumbnail.setAttribute('onerror', 'fn.handleerroriconnotfound.call(this)');
        left.appendChild(this.itemTemplateThumbnail);
        this.itemTemplate.appendChild(left);
        var center = document.createElement("div");
        center.setAttribute("class", "center");
        center.setAttribute("style", "width: 100%;");
        this.itemTemplateListItemTitle = document.createElement("span");
        this.itemTemplateListItemTitle.setAttribute("class", "list-item__title overflow-ellipsis");
        this.itemTemplateListItemTitle.setAttribute("style", "padding-right: 50px;");
        center.appendChild(this.itemTemplateListItemTitle);
        var subtitle = document.createElement("span");
        subtitle.setAttribute("class", "list-item__subtitle");
        subtitle.setAttribute("style", "width: 100%;");
        this.itemTemplateSubtitleFeedTitle = document.createElement("span");
        this.itemTemplateSubtitleFeedTitle.setAttribute("class", "subtitle-feed-title");
        subtitle.appendChild(this.itemTemplateSubtitleFeedTitle);
        this.itemTemplateSubtitleDescription = document.createElement("div");
        this.itemTemplateSubtitleDescription.setAttribute("class", "subtitle-description overflow-ellipsis");
        subtitle.appendChild(this.itemTemplateSubtitleDescription);
        this.itemTemplateSubTitleTimeago = document.createElement("span");
        this.itemTemplateSubTitleTimeago.setAttribute("class", "subtitle-timeago");
        subtitle.appendChild(this.itemTemplateSubTitleTimeago);
        center.appendChild(subtitle);
        this.itemTemplate.appendChild(center);
        // END Build list item template
        return this.itemTemplate;
    }
}

router.addPage(new PageRoute(
    'news.html',
    'news-page',
    pageElement => new NewsListPage(pageElement)
));

export default NewsListPage;
