import storage from './storage';
import { EventEmitter } from "events";

const db = storage.constructor.getDB();
const name = 'favorites';

class Favorites extends EventEmitter {

    constructor() {
        super();
    }

    addFeed = async feed => {
        const favorites = await db.get(name);
        favorites[feed.url] = true;
        await db.set(name, favorites); 
        this.emit('change');
    }

    removeFeed = async feed => {
        const favorites = await db.get(name);
        if (favorites[feed.url]) delete favorites[feed.url];
        await db.set(name, favorites);
        this.emit('change');
    }

    getFavoriteFeeds = async () => {
        const favorites = await db.get(name);
        const feeds = await storage.getFeeds();
        const favoriteFeeds = {};
        const retArr = [];
        for (const url in favorites) {
            if (feeds[url]) {
                favoriteFeeds[url] = true;
                retArr.push(feeds[url]);
            }
        }
        db.set(name, favoriteFeeds);
        return retArr;
    }
}

export default new Favorites();
