import router from './router';
import Page from './page';
import PageRoute from './page-route';
import { isTabbarChild, logError } from './util';
import { parseOPMLFile } from './opml';


class AddOPMLPage extends Page {
    constructor(pageElement) {
        super(pageElement);

        var addOPMLOpenURL = document.getElementById('add-opml-open-url');
        var addOPMLOpenURLBtn = document.getElementById('add-opml-open-url-btn');
        var addOPMLOpenFile = document.getElementById('add-opml-open-file');
        var toolbar = pageElement.getElementsByClassName('toolbar')[0];
        if (isTabbarChild(pageElement)) {
            toolbar.parentElement.removeChild(toolbar);
        }

        addOPMLOpenURLBtn.addEventListener('click', function () {
            var url = addOPMLOpenURL.value;

            var urlIsValid = validURL(url);

            if (urlIsValid === false) {
                ons.notification.alert('The provided url is not valid.', { cancelable: true });
                return;
            }

            router.navigator.pushPage('add-opml-inspect.html', {
                data: {
                    opmlGetter: function (cb) {
                        parseOPMLURL(url, cb);
                    }
                }
            });
        });

        addOPMLOpenFile.addEventListener('input', function (e) {
            var file = e.target.files[0];
            if (!file) return;

            const opmlGetter = parseOPMLFile(file);

            router.navigator.pushPage('add-opml-inspect.html', {
                data: {
                    opmlGetter: opmlGetter
                }
            });
            opmlGetter.catch(logError).then(() => {
                e.target.value = '';
            });
        });
    }
}

router.addPage(new PageRoute(
    'add-opml.html',
    'add-opml-page',
    pageElement => new AddOPMLPage(pageElement)));

export default AddOPMLPage;
