import storage from './storage';
import { arrayEqual } from './util';
import { EventEmitter } from 'events';

export default class Category {
    constructor(title, parent) {
        this.title = title;
        this.feeds = {};
        this.categories = {};
        this.parent = parent;
    }

    static promiseFromFeedStorage = () => {
        return storage.getFeeds().then(Category.fromFeedsObj);
    }

    static fromFeedsObj = feeds => {//getCategoriesFromFeedsObj
        var rootCategory = new Category('root', null);

        for (var url in feeds) {
            var feed = feeds[url];
            var category = (feed.category || []).slice(0);
            var tempCategory = rootCategory;
            for (var i = 0; i < category.length; i++) {
                var sub = category[i];
                if (sub in tempCategory.categories) {
                    tempCategory = tempCategory.categories[sub];
                } else {
                    tempCategory = tempCategory.categories[sub] = new Category(sub, tempCategory);
                }
            }
            tempCategory.feeds[url] = true;
        }
        return rootCategory;
    }

    static getScopedCategories = async path => {
        const root = await Category.promiseFromFeedStorage();
        return root.getScopedCategoryFromRoot(path);
    }

    static checkIfCategoryIsChild(supposedParentPath, supposedChildPath) {
        if (supposedChildPath.length < supposedParentPath.length) return false;
        for (var i = 0; i < supposedParentPath.length; i++) {
            if (supposedParentPath[i] !== supposedChildPath[i]) return false;
        }
        return true;
    }

    static fromJSON = obj => {
        const instance = new Category(obj.title, obj.parent);
        instance.feeds = obj.feeds;
        instance.categories = obj.categories;
    }

    getScopedCategoryFromRoot = path => {
        let root = this;
        for (var i = 0; i < path.length; i++) {
            var sub = path[i];
            if (!root.categories[sub]) {
                root.categories[sub] = new Category(sub, root);
            }
            root = root.categories[sub] || new Category(sub, root);
        }
        return root;
    }

    getCategoryPath = () => {
        let category = this;
        const path = [];
        while (category.parent !== null) {
            path.unshift(category.title);
            category = category.parent;
        }
        return path;
    }

    containsFeed = feed => {
        return arrayEqual(feed.category, this.getCategoryPath());
    }

    getCategoryTreeRoot = () => {
        let category = this;
        while (category.parent !== null) {
            category = category.parent;
        }
        return category;
    }

    moveTo = async newPath => {
        // check if new path is not within the category sub tree
        var treeRoot = this.getCategoryTreeRoot();
        var oldPath = this.getCategoryPath();
        var isChild = Category.checkIfCategoryIsChild(oldPath, newPath);
        var equal = arrayEqual(oldPath, newPath);

        if (isChild) {
            Promise.reject({
                messsage: 'Category is child',
                name: 'category-is-child'
            });
        }
        if (equal) return;

        var newParent = treeRoot.getScopedCategoryFromRoot(newPath.slice(0, -1));
        if (newPath[newPath.length - 1] in newParent.categories) {
            Promise.reject({
                messsage: 'A Category with the same name already exists',
                name: 'already-exists'
            });
        }

        newParent.categories[this.title] = this;
        this.parent = newParent;

        const feeds = await storage.getFeeds();

        (function recurseFeeds(cat) {
            for (let title in cat.categories) {
                recurseFeeds(cat.categories[title]);
            }
            for (let url in cat.feeds) {
                var feed = feeds[url];
                feed.category =  cat.getCategoryPath();
                storage.putFeed(feed);
            }
        })(this);

    }

    getScopedCategories = path => {
        let root = this;
        for (var i = 0; i < path.length; i++) {
            var sub = path[i];
            if (!root.categories[sub]) {
                root.categories[sub] = new Category(sub, root);
            }
            root = root.categories[sub] || new Category(sub, root);
        }
        return root;
    }

    toJSON = () => ({
        title: this.title,
        feeds: this.feeds,
        categories: this.categories,
        parent: this.parent
    })
}
