import Category from './categories';
import router from './router';
import Page from './page';
import PageRoute from './page-route';
import { clearChildren, iconFinder, cloneWithoutChildren } from './util';

class SplitterPage extends Page {
    constructor(pageElement) {
        super(pageElement);
        var menu = document.getElementById('menu');
        var categoryItems = document.getElementById('splitter-category-items');

        document.getElementById('menu').addEventListener('preopen', function () {
            menu.style.boxShadow = '0 0 30px rgba(0, 0, 0, 0.6)';
        });
        document.getElementById('menu').addEventListener('preclose', function () {
            menu.style.boxShadow = null;
        });

        document.addEventListener('categorieschanged', handleCategoriesChanged);

        function handleCategoriesChanged(e) {
            render();
        }

        render();

        function render() {
            Category.promiseFromFeedStorage().then(function (cat) {
                categoryItems = cloneWithoutChildren(categoryItems);
                for (let title in cat.categories) {
                    var category = cat.categories[title];
                    renderCategoryItem(category, categoryItems);
                }
            });
        }

        function renderCategoryItem(category, list) {
            var listItem = document.createElement('ons-list-item');
            listItem.setAttribute('tappable', true);

            var left = document.createElement('div');
            left.setAttribute('class', 'left');
            listItem.appendChild(left);

            var onsIcon = document.createElement('ons-icon');
            onsIcon.setAttribute('icon', 'md-' + (iconFinder(category.title) || 'folder'));
            left.appendChild(onsIcon);

            var center = document.createElement('div');
            center.setAttribute('class', 'center');
            center.innerText = category.title;
            listItem.appendChild(center);

            var right = document.createElement('div');
            right.setAttribute('class', 'right');
            listItem.appendChild(right);

            listItem.addEventListener('click', function () {
                router.navigator.pushPage('category.html', {
                    data: {
                        category: category.getCategoryPath()
                    }
                });
            });

            list.appendChild(listItem);
        }
    }
}

router.addPage(new PageRoute(
    'splitter.html',
    'splitter',
    pageElement => new SplitterPage(pageElement)
));

export default SplitterPage;
