import IndexedDBStorage from './indexeddb-wrapper';
import ons from 'onsenui';
import { logError } from './util';
import FeedItem from './feed-item';
import Feed from './feed';
import RssFeed from './rss-feed';
import AtomFeed from './atom-feed';

window.storageEstimateWrapper = function storageEstimateWrapper() {
    if ('storage' in navigator && 'estimate' in navigator.storage) {
        return navigator.storage.estimate();
    }

    if ('webkitTemporaryStorage' in navigator
        && 'queryUsageAndQuota' in navigator.webkitTemporaryStorage) {
        return new Promise(function (resolve, reject) {
            navigator.webkitTemporaryStorage.queryUsageAndQuota(
                function (usage, quota) { resolve({ usage: usage, quota: quota }); },
                reject
            );
        });
    }

    return Promise.resolve({ usage: NaN, quota: NaN });
};

function savableFormat(feeds) {
    var retObj = {};
    Object.keys(feeds).forEach(function (key) {
        var newObj = Object.assign({}, feeds[key]);
        delete newObj.items;
        retObj[key] = newObj;
    });
    return retObj;
}

const indexedDBStorage = new IndexedDBStorage('data');
const indexDBItemPrefix = 'feed-items-';
const feedsLocalstorageName = 'newsfeeder-feeds-storage-v2';
let cachedFeeds = undefined;

let lastPromise = Promise.resolve();

const getFeeds = () => {
    return new Promise(function (resolve, reject) {
        if (cachedFeeds !== undefined) {
            resolve(cachedFeeds);
        } else {
            const set = JSON.parse(localStorage.getItem(feedsLocalstorageName) || '{}');
            let feedItemsPromises = Object.keys(set).map(function (feedUrl) {
                return indexedDBStorage.get(indexDBItemPrefix + feedUrl)
                    .then(function (feedItems) {
                        set[feedUrl].items = feedItems || [];
                    }).catch(err => {
                        logError(err)
                        reject();
                    });
            });
            Promise.all(feedItemsPromises).then(function () {
                for (const url in set) {
                    switch (set[url].type) {
                        case 'rss':
                            set[url] = RssFeed.fromJSON(set[url]);
                            break;
                        case 'atom':
                            set[url] = AtomFeed.fromJSON(set[url]);
                            break;
                        default:
                            throw new Error(`Unknown feed type ${set[url].type}`);
                    }
                }
                cachedFeeds = set;
                resolve(cachedFeeds);
            }).catch(err => {
                logError(err)
                reject();
            });
        }
    });
}

class Storage {
    constructor() {
        // MIGRATE:
        if (localStorage.getItem('feeds')) {
            ons.ready(function () {
                ons.notification.toast('Patching older version. Your browser window will reload...');
            });
            var feeds = JSON.parse(localStorage.getItem('feeds') || '{}');
            var patchPromises = [];
            for (var url in feeds) {
                var feed = feeds[url];
                patchPromises.push(this.putFeed(feed));
            }
            Promise.all(patchPromises).then(function () {
                localStorage.removeItem('feeds');
                alert('We\'ve made some changes to the way we store our news feeds. To make sure all of your older feeds aren\'t lost, we have patched them to the newer version. The app will now reload. Please continue.');
                window.location.reload(true);
            });
        }
    }

    static isFirstTimeAppOpening = () => {
        return !localStorage.getItem(feedsLocalstorageName);
    }

    static storageEstimateWrapper = () => storageEstimateWrapper();

    static getDB = () => indexedDBStorage;

    putFeed = (parsedFeed, skipLastPromise = false) => {
        return lastPromise = new Promise((resolve, reject) => {
            (skipLastPromise ? Promise.resolve() : lastPromise).then(() => {
                return getFeeds();
            }).then(function (feeds) {
                feeds[parsedFeed.url] = parsedFeed;
                cachedFeeds = feeds;
                return indexedDBStorage.set(indexDBItemPrefix + parsedFeed.url, (parsedFeed.items.map(x => x.toJSON()) || [])).then(function () {
                    var fmt = savableFormat(feeds);
                    try {
                        localStorage.setItem(feedsLocalstorageName, JSON.stringify(fmt));
                    } catch (domException) {
                        if (domException.name === 'QuotaExceededError'
                            || domException.name === 'NS_ERROR_DOM_QUOTA_REACHED') {
                            alert('Unable to store data because you\'ve reached the maximum storage quota.');
                        }
                    }
                    resolve();
                });
            }).catch(err => {
                logError(err)
                resolve();
            });
        });
    }

    shouldWeakPut = (parsedFeed, skipLastPromise = false) => {
        return lastPromise = new Promise((resolve, reject) => {
            // only put if feed doesn't already exist 
            // and if the older feed has another should_update_token


            (skipLastPromise ? Promise.resolve() : lastPromise).then(() => {
                return getFeeds();
            }).then(function (feeds) {
                var should = !feeds[parsedFeed.url]
                    || feeds[parsedFeed.url].should_update_token !== parsedFeed.should_update_token;
                resolve(should);
            }).catch(err => {
                logError(err)
                resolve(true);
            });
        });
    }
    tryWeakPut = parsedFeed => {
        // only put if feed doesn't already exist 
        // and if the older feed has another should_update_token
        return lastPromise = new Promise((resolve, reject) => {
            lastPromise.then(() => {
                return this.shouldWeakPut(parsedFeed, true);
            }).then((shouldWeakPut) => {
                if (shouldWeakPut) {
                    this.putFeed(parsedFeed, true).then(() => {
                        resolve(true);
                    });
                } else {
                    resolve(false);
                }
            }).catch(resolve);
        });
    }
    getFeeds = () => {
        return lastPromise = lastPromise.then(function () {
            return getFeeds();
        });
    }
    deleteFeed = feed => {
        return this.deleteFeedURL(feed.url);
    }
    deleteFeedURL = feedURL => {
        return lastPromise = new Promise(function (resolve, reject) {
            lastPromise.then(function () {
                return getFeeds();
            }).then(function (feeds) {
                return indexedDBStorage.delete(indexDBItemPrefix + feedURL).then(function () {
                    delete feeds[feedURL];
                    cachedFeeds = feeds;
                    try {
                        localStorage.setItem(feedsLocalstorageName, JSON.stringify(cachedFeeds));
                    } catch (domException) {
                        if (domException.name === 'QuotaExceededError'
                            || domException.name === 'NS_ERROR_DOM_QUOTA_REACHED') {
                            alert('Unable to store data because you\'ve reached the maximum storage quota.');
                        }
                    }
                    resolve();
                });
            }).catch(resolve);
        });
    }
}

export default new Storage();
