import router from './router';
import Page from './page';
import PageRoute from './page-route';
import storage from './storage';
import { byteSizeFormat, excerpt, cloneWithoutChildren } from './util';
import { Pie } from 'chartist';
import favorites from './favorites';
import bookmarks from './bookmarks';

class StoragePage extends Page {
    constructor(pageElement) {
        super(pageElement);
        var storageQuota = document.getElementById('storage-quota');
        var storageUsagePercentage = document.getElementById('storage-usage-percentage');
        var storageUsage = document.getElementById('storage-usage');
        var storageLimit = document.getElementById('storage-limit');
        var bar = document.getElementById('storage-quota-bar');
        var items = document.getElementById('storage-app-items');
        var otherStorageBytes = document.getElementById('other-storage-bytes');

        const bookmarkStorageAmount = document.getElementById('bookmark-storage-amount');
        const favoritesStorageAmount = document.getElementById('favorite-storage-amount');

        document.addEventListener('feedsrefetch', renderList);

        pageElement.addEventListener('destroy', function () {
            document.removeEventListener('feedsrefetch', renderList);
        });

        pageElement.addEventListener('show', function () {
            renderList();
        });

        function renderList() {
            storage.getFeeds().then(function (feeds) {
                items = cloneWithoutChildren(items);
                var totalSize = 0;
                var feedSizes = [];
                for (var url in feeds) {
                    var feed = feeds[url];
                    var size = JSON.stringify(feed).length;
                    totalSize += size;
                    feedSizes.push({ feed: feed, size: size });
                }
                feedSizes.sort(function (a, b) {
                    return b.size - a.size
                });
                feedSizes.forEach(function (feedSize) {
                    var feed = feedSize.feed;
                    var size = feedSize.size;
                    var percentage = size / totalSize * 100;
                    var listItem = createStorageListItem(feed, size, percentage);
                    items.appendChild(listItem);
                });

                var totalSizeWithFavoritesAndBookmarks = totalSize;

                (async () => {
                    const favs = await favorites.getFavoriteFeeds();
                    const favLen = JSON.stringify(favs).length - 2;
                    totalSizeWithFavoritesAndBookmarks += favLen;
                    favoritesStorageAmount.textContent = byteSizeFormat(favLen, 'short');
                    const marks = await bookmarks.getItems();
                    const bookmarkLen = JSON.stringify(marks).length - 2;
                    totalSizeWithFavoritesAndBookmarks += bookmarkLen;
                    bookmarkStorageAmount.textContent = byteSizeFormat(bookmarkLen, 'short');
                })();

                storageEstimateWrapper().then(function (estimate) {
                    if (estimate.usage === NaN) return;
                    storageQuota.setAttribute('style', '');

                    var percentage = estimate.usage / estimate.quota * 100;
                    storageUsagePercentage.innerText = Math.round(percentage);
                    storageUsage.innerText = byteSizeFormat(estimate.usage, 'short');
                    storageLimit.innerText = byteSizeFormat(estimate.quota, 'short');

                    bar.setAttribute('style', 'right: ' + (100 - percentage) + '%');
                    otherStorageBytes.innerText = byteSizeFormat(
                        Math.max(0, estimate.usage - totalSizeWithFavoritesAndBookmarks), 'short');
                });

                var data = {
                    labels: feedSizes.map(({feed, size}) => feed.title),
                    series: feedSizes.map(({feed, size}) => size)
                };

                var options = {
                    labelInterpolationFnc: function (value) {
                        return excerpt(value, 6);
                    }
                };

                var responsiveOptions = [
                    ['screen and (min-width: 640px)', {
                        // chartPadding: 30,
                        // labelOffset: 100,
                        // labelDirection: 'explode',
                        labelInterpolationFnc: function (value) {
                            return excerpt(value, 14);
                        }
                    }],
                    ['screen and (min-width: 1024px)', {
                        // labelOffset: 80,
                        // chartPadding: 20,
                        labelInterpolationFnc: function (value) {
                            return excerpt(value, 30);
                        }
                    }]
                ];

                new Pie('.ct-chart', data, options, responsiveOptions);
            });
        }

        function createStorageListItem(feed, size, percentage) {
            var siteUrlParts = feed.site.split('/');
            var favicon = siteUrlParts[0].replace('http:', 'https:') + '//' + siteUrlParts[2] + '/favicon.ico';

            var listItem = document.createElement('ons-list-item');
            listItem.setAttribute('tappable', true);
            listItem.setAttribute('modifier', 'chevron');

            var left = document.createElement('div');
            left.setAttribute('class', 'left');
            listItem.appendChild(left);

            var img = document.createElement('img');
            img.setAttribute('onerror', 'fn.handleerroriconnotfound.call(this)');
            img.setAttribute('src', favicon);
            img.setAttribute('class', 'list-item__thumbnail');
            left.appendChild(img);

            var center = document.createElement('div');
            center.setAttribute('class', 'center');
            listItem.appendChild(center);

            var title = document.createElement('span');
            title.setAttribute('class', 'list-item__title overflow-ellipsis');
            title.innerText = feed.title;
            center.appendChild(title);

            var right = document.createElement('div');
            right.setAttribute('class', 'right');
            right.innerText = byteSizeFormat(size, 'short');

            listItem.addEventListener('click', function () {
                router.navigator.pushPage('specific-feed.html', {
                    data: {
                        url: feed.url,
                    }
                });
            });

            listItem.appendChild(right);
            return listItem;
        }
    }
}

router.addPage(new PageRoute(
    'storage.html',
    'storage-page',
    pageElement => new StoragePage(pageElement)
));

export default StoragePage;

