import Page from "./page";
import router from './router';
import PageRoute from "./page-route";
import storage from "./storage";
import { getOPMLXML } from "./opml";
import Category from "./categories";
import { logError } from "./util";
import * as ons from 'onsenui';

class DownloadOPMLPage extends Page {
    constructor(pageElement) {
        super(pageElement);
        this.downloadOPMLLink = pageElement.querySelector('#download-opml-link');
        this.init();
    }

    init = async () => {
        const feeds = await storage.getFeeds();
        const root = Category.fromFeedsObj(feeds);
        try {
            var opmlXML = getOPMLXML(feeds, root);
            this.downloadOPMLLink.href = 'data:text/xml,' + encodeURIComponent(opmlXML);
        } catch (err) {
            logError(err);
            ons.notification.alert('Unable to create an OPML file.', { cancelable: true });
        }
    }
}

router.addPage(new PageRoute(
    'download-opml.html',
    'download-opml-page',
    pageElement => new DownloadOPMLPage(pageElement)));

export default DownloadOPMLPage;
