import storage from "./storage";
import Feed from "./feed";
import * as ons from 'onsenui';
import { feedsRefetch } from "./feed-helpers";
import { setDefaultFeeds, getParameterByName } from "./util";
const isFirstTimeAppOpening = storage.constructor.isFirstTimeAppOpening;

function firstTimeAppOpening() {
    setDefaultFeeds();
    window.fn.load('recommended.html');
    window.fn.open();
    feedsRefetch();
}

if (!isFirstTimeAppOpening()) {
    feedsRefetch();
}

class Router {
    constructor() {
        const navigator = this.navigator = document.getElementById('navigator');

        this.pageRoutes = [];
        ons.ready(function () {
            const feedUrl = getParameterByName('feed');
            if (feedUrl) {
                const feedPromise = storage.getFeeds();
                setTimeout(async () => {
                    const feeds = await feedPromise;
                    navigator.pushPage(feedUrl in feeds 
                        ? 'specific-feed.html'
                        : 'add-feed.html', {
                        data: {
                            url: feedUrl,
                        }
                    });
                }, 300);
            }
        });


        document.addEventListener('init', this.handleDocumentInit);
    }

    addPage = pageRoute => {
        this.pageRoutes.push(pageRoute);
    }

    handleDocumentInit = e => {
        const page = e.target;
        const pageRoute = this.pageRoutes.find(pageRoute => {
            return pageRoute.pageId === page.id
                || pageRoute.pageId === page.getAttribute('data-id');
        });
        if (pageRoute) {
            if (page.id !== 'splitter' && isFirstTimeAppOpening()) {
                firstTimeAppOpening();
            } else {
                pageRoute.factory(page);
            }
        }
    }
}

export default new Router();

