import router from "./router";
import * as ons from 'onsenui';

const sheetPromise = ons.createElement('background-audio-action-sheet.html', { append: true })
let _audioElement = null;

sheetPromise.then(sheet => {
    sheet.querySelector('.sheet-item-close').addEventListener('click', () => {
        sheet.hide();
    });
    sheet.querySelector('.sheet-item-stop').addEventListener('click', () => {
        removeBackgroundAudio();
        sheet.hide();
    });
});

export const setBackgroundAudio = async (audioElement) => {
    if (_audioElement) await removeBackgroundAudio();
    _audioElement = audioElement;

    const sheet = await sheetPromise;
    const wrapper = sheet.querySelector('#background-audio-action-sheet-audio-wrapper');
    wrapper.appendChild(audioElement);

    pageInitOrShow(fn.navigator.topPage);
}   

export const getBackgroundAudio = () => {
    return _audioElement;
}

export const removeBackgroundAudio = async () => {
    const sheet = await sheetPromise;
    const wrapper = sheet.querySelector('#background-audio-action-sheet-audio-wrapper');
    wrapper.removeChild(_audioElement);
    _audioElement = null;

    pageInitOrShow(fn.navigator.topPage);
}

document.addEventListener('init', e => {
    pageInitOrShow(e.target);
});

document.addEventListener('show', e => {
    pageInitOrShow(e.target);
});

const pageInitOrShow = page => {
    const pageRoute = router.pageRoutes.find(pageRoute => {
        return pageRoute.pageId === page.id
            || pageRoute.pageId === page.getAttribute('data-id');
    });
    if (!pageRoute) return;
    const toolbarRight = page.querySelector('ons-toolbar .right');
    if (!toolbarRight) return;

    const existingAudioToolbarBtn = toolbarRight.querySelector('.background-audio-toolbar-btn');
    if (!_audioElement) {
        if (existingAudioToolbarBtn) toolbarRight.removeChild(existingAudioToolbarBtn);
    } else if (_audioElement) {
        if (!existingAudioToolbarBtn) {
            toolbarRight.appendChild(createBackgroundAudioToolbarIcon());
        }
    }
};


const createBackgroundAudioToolbarIcon = () => {
    var toolbarBtn = document.createElement("ons-toolbar-button");
    toolbarBtn.addEventListener('click', async () => {
        const sheet = await sheetPromise;
        if (!_audioElement) return;
        sheet.show();
    });
    toolbarBtn.setAttribute('class', 'background-audio-toolbar-btn');
    var icon = document.createElement("ons-icon");
    icon.setAttribute("icon", "ion-volume-high");
    toolbarBtn.appendChild(icon);
    return toolbarBtn;
}
