import FeedItem from "./feed-item";
import storage from './storage';
import { EventEmitter } from "events";

const db = storage.constructor.getDB();
const name = 'bookmarks';

class BookMarkItem {
    constructor(item, feed) {
        this.item = item.toJSON ? item.toJSON() : item; 
        this.feed = {
            title: feed.title,
            icon: feed.icon
        };
    }

    toJSON = () => {
        return {
            item: this.item,
            feed: this.feed
        };
    }
}

class Bookmarks extends EventEmitter {
    constructor() {
        super();
    }

    addItem = async (item, feed) => {
        const bookmark = new BookMarkItem(item, feed).toJSON();
        const bookmarks = await db.get(name) || [];
        bookmarks.push(bookmark);
        await db.set(name, bookmarks);
        this.emit('change');
    }

    removeItem = async (item) => {
        const bookmarks = await db.get(name) || [];
        const toRemove = bookmarks.find(b => b.item.link === item.link);
        const i = bookmarks.indexOf(toRemove);
        bookmarks.splice(i, 1);
        await db.set(name, bookmarks);
        this.emit('change');
    }

    getItems = async () => {
        const bookmarks = await db.get(name) || [];
        return bookmarks;
    }

}

export default new Bookmarks();
