import router from "./router";
import PageRoute from "./page-route";
import { excerpt, cloneWithoutChildren } from "./util";
import favorites from "./favorites";


class FavoritesPage {
    constructor(pageElement) {
        this.pageElement = pageElement;
        this.itemTemplate = this.createTemplate();
        this.listItems = document.getElementById('favorites-list');
        this.init();

        pageElement.addEventListener('destroy', this.destroy);
        pageElement.addEventListener('show', this.show);
        favorites.on('change', this.handleFavoritesChange);
    }

    destroy = () => {
        favorites.removeListener('change', this.handleFavoritesChange);
    }

    show = () => {

    }

    handleFavoritesChange = async () => {
        this.favoriteFeeds = await favorites.getFavoriteFeeds()
        this.render();
    }

    init = async () => {
        this.favoriteFeeds = await favorites.getFavoriteFeeds()
        this.render();
    }

    render = () => {
        this.listItems = cloneWithoutChildren(this.listItems);
        
        this.favoriteFeeds.map(feed => {
            const url = feed.url;            
            const elem = document.createElement('ons-list-item');
            elem.setAttribute('tappable', true);
            elem.setAttribute('modifier', 'chevron');
            const left = document.createElement('div');
            left.setAttribute('class', 'left');
            elem.appendChild(left);
            const img = document.createElement('img');
            img.setAttribute('onerror', 'fn.handleerroriconnotfound.call(this)');
            img.setAttribute('src', feed.icon || 'images/feediconnotfound.png');
            img.setAttribute('class', 'list-item__thumbnail');
            left.appendChild(img);
            const center = document.createElement('div');
            center.classList.add('center');
            elem.appendChild(center);
            const title = document.createElement('span');
            title.textContent = feed.title || '';
            title.classList.add('list-item__title');
            center.appendChild(title);
            const subtitle = document.createElement('span');
            subtitle.textContent = excerpt(feed.description, 200) || '';
            subtitle.classList.add('list-item__subtitle')
            center.appendChild(subtitle);
            elem.addEventListener('click', function () {
                router.navigator.pushPage('specific-feed.html', {
                    data: {
                        url: url,
                    }
                });
            });
            this.listItems.appendChild(elem);
        });
        if (this.favoriteFeeds.length === 0) {
            const empty = document.createElement('ons-list-item');
            empty.innerText = 'You haven\'t saved any favorite feeds yet.';
            this.listItems.appendChild(empty);
        }
    }

    createTemplate = () => {
        // START Build list item template
        this.itemTemplate = document.createElement("ons-list-item");
        this.itemTemplate.setAttribute("tappable", "");
        this.itemTemplate.setAttribute("modifier", "chevron");
        const left = document.createElement("div");
        left.setAttribute("class", "left");
        this.itemTemplateThumbnail = document.createElement("img");
        this.itemTemplateThumbnail.setAttribute("class", "list-item__thumbnail");
        this.itemTemplateThumbnail.setAttribute('onerror', 'fn.handleerroriconnotfound.call(this)');
        left.appendChild(this.itemTemplateThumbnail);
        this.itemTemplate.appendChild(left);
        const center = document.createElement("div");
        center.setAttribute("class", "center");
        center.setAttribute("style", "width: 100%;");
        this.itemTemplateListItemTitle = document.createElement("span");
        this.itemTemplateListItemTitle.setAttribute("class", "list-item__title overflow-ellipsis");
        this.itemTemplateListItemTitle.setAttribute("style", "padding-right: 50px;");
        center.appendChild(this.itemTemplateListItemTitle);
        const subtitle = document.createElement("span");
        subtitle.setAttribute("class", "list-item__subtitle");
        subtitle.setAttribute("style", "width: 100%;");
        this.itemTemplateSubtitleFeedTitle = document.createElement("span");
        this.itemTemplateSubtitleFeedTitle.setAttribute("class", "subtitle-feed-title");
        subtitle.appendChild(this.itemTemplateSubtitleFeedTitle);
        this.itemTemplateSubtitleDescription = document.createElement("div");
        this.itemTemplateSubtitleDescription.setAttribute("class", "subtitle-description overflow-ellipsis");
        subtitle.appendChild(this.itemTemplateSubtitleDescription);
        this.itemTemplateSubTitleTimeago = document.createElement("span");
        this.itemTemplateSubTitleTimeago.setAttribute("class", "subtitle-timeago");
        subtitle.appendChild(this.itemTemplateSubTitleTimeago);
        center.appendChild(subtitle);
        this.itemTemplate.appendChild(center);
        // END Build list item template
        return this.itemTemplate;
    }


}

router.addPage(new PageRoute(
    'favorites.html',
    'favorites-page',
    pageElement => new FavoritesPage(pageElement)));

export default FavoritesPage;

