import AtomFeed from "./atom-feed";
import RssFeed from "./rss-feed";
import storage from './storage';
import URL from './urls';
import { userLang } from "./localization";
import { logError, debounce, throttle } from "./util";

var proxy = 'https://cors-anywhere.herokuapp.com/';

export const feedsRefetch = throttle(async () => {
    console.log('feeds refetch');
    const feeds = await storage.getFeeds();
    var feedsArr = [];
    for (var url in feeds) feedsArr.push(feeds[url]);
    let progress = 0;

    await Promise.all(feedsArr.map(async feed => {
        try {
            progress += 100 / feedsArr.length;
            const newFeed = await fetchFeed(feed.url);
            newFeed.category = feed.category;
            const result = storage.tryWeakPut(newFeed);
            console.log(result);
            if (result === true) {
                console.log('try to fire feeds refetch');
                fireThrottledFeedsRefetchEvent(progress);
            }
        } catch (err) {
            logError(err);
        }
    }));
    fireThrottledFeedsRefetchEvent();
}, 1000 * 60 * 5 /* 5 minutes */);

export const fireThrottledFeedsRefetchEvent = throttle((percentage = 100) => {
    var event = new CustomEvent('feedsrefetch', { detail: percentage });
    document.dispatchEvent(event);
    console.log('feedsrefetch!');
}, 2000);

