export const fetchXML = (url) => {
    return new Promise((resolve, reject) => {
        var xhr = new XMLHttpRequest();
        xhr.timeout = 15 * 1000; // 15 seconds
        xhr.onreadystatechange = function () {
            if (this.readyState === 4) {
                if (this.status === 200) {
                    if (this.responseXML) resolve(this);
                    else reject({
                        type: 'network',
                        error: 'Feed is not returning valid XML'
                    });
                } else reject({
                    type: 'network',
                    error: this.responseText || 'A network error occurred.',
                });
            }
        };
        if (location.protocol !== ':file') url = proxy + url;
        xhr.open("GET", url, true);
        xhr.setRequestHeader("Accept", "application/rss+xml,application/xml,text/xml");
        xhr.send();
    });
}

export const fetchFeed = async url => {
    try {
        const response = await fetch('https://api.esstudio.site/api/newsfeeder/json-feed?url=' + encodeURIComponent(url));
        const json = await response.json();
        if (json.code === 'feed-error') throw json;
        const feed = json.type === 'rss'
            ? RssFeed.fromJSON(json)
            : AtomFeed.fromJSON(json);
        return feed;
    } catch (err) {
        throw err;
    }
}

export const findFeedlyFeed = async q => {
    try {
        // const response = ;
        const response = location.protocol !== ':file'
            ? await fetch(`https://ess-server.herokuapp.com/api/feedly/get-feed-id?query=${encodeURIComponent(q)}&locale=${encodeURIComponent(userLang)}`)
            : await fetch(`https://feedly.com/v3/search/feeds?query=${encodeURIComponent(q)}&locale=${encodeURIComponent(userLang)}`)
        const json = await response.json();
        if (json) {
            return json.replace(/^feed\//, '');
        } else {
            throw ({
                type: 'notfound',
                error: 'Unable to find feed.'
            });
        }
    } catch (err) {
        logError(err);
        throw ({
            type: 'network',
            error: 'Unable to search for feed.'
        });
    }
}


