import router from './router';
import * as ons from 'onsenui';

document.addEventListener("DOMContentLoaded", function (event) {
    var websiteTitle = document.title;
    if (window.cordova && cordova.platformId === 'browser' && window.history) {
        router.navigator.addEventListener('postpush', function (e) {
            const page = e.detail.enterPage;
            if (page.id === 'splitter') return;
            var title = page.querySelector('.toolbar__center').innerText;
            document.title = websiteTitle + ' - ' + title;
            if (page.id === 'specific-feed-page') {
                window.history.pushState(null, document.title, '?feed=' + encodeURIComponent(page.data.url));
            } else {
                window.history.pushState(null, document.title, './');
            }
        });
        var historyAPI = true;
        router.navigator.addEventListener('postpop', function (e) {
            historyAPI = !!e.detail.enterPage.data.historyAPI;
            if (!e.detail.enterPage.data.historyAPI) history.back();
        });
        window.addEventListener('popstate', function (e) {
            if (historyAPI) router.navigator.popPage({ data: { historyAPI: true } });
            historyAPI = true;
        });
    }
});