import router from './router';
import Page from './page';
import PageRoute from './page-route';
import storage from './storage';
import Category from './categories';
import * as ons from 'onsenui';
import { clearChildren, cloneWithoutChildren } from './util';

class CategoryPage extends Page {
    constructor(pageElement) {
        super(pageElement);
        // use page.data if the page is pushed
        // or use fn.category.data if the page was opened with ons-splitter-side
        var isPush = !!pageElement.data;
        var data = isPush ? pageElement.data : fn.category.data;

        this.subCategoryItems = pageElement.querySelector('.category-sub-category-items');
        this.subCategoryAddBtn = pageElement.querySelector('.category-sub-category-add .fab');
        this.categoryFeeds = pageElement.querySelector('.category-page-category-feeds');
        this.toolbarTitle = pageElement.querySelector('.category-page-title');
        this.categoryCreateMessage = pageElement.querySelector('.category-create-message');
        this.categoryEditListItem = pageElement.querySelector('.category-sub-category-edit');
        this.categoryEditFab = pageElement.querySelector('.category-sub-category-edit .fab');

        if (isPush) {
            var left = pageElement.querySelector('.left');
            clearChildren(left);
            left.appendChild(document.createElement('ons-back-button'));
        }

        this.categoryTitle = data.category;
        var isRoot = this.categoryTitle.length === 0;
        this.toolbarTitle.innerText = isRoot ? 'Uncategorized' : this.categoryTitle[this.categoryTitle.length - 1];
        if (isRoot) {
            this.categoryCreateMessage.innerText = 'Create a new category';
            this.categoryEditListItem.parentElement.removeChild(this.categoryEditListItem);
        }

        const addNewCategory = () => {
            router.navigator.pushPage('add-category.html', {
                data: {
                    path: this.categoryTitle,
                    selectedFeeds: []
                }
            });
        }

        this.subCategoryAddBtn.addEventListener('click', addNewCategory);
        this.categoryEditFab.addEventListener('click', this.handleCategoryEditClick);

        document.addEventListener('categorieschanged', this.handleCategoriesChange);

        pageElement.addEventListener('destroy', this.destroy);

        storage.getFeeds().then((feeds) => {
            const root = Category.fromFeedsObj(feeds);
            this.category = root.getScopedCategories(this.categoryTitle);
            this.feeds = feeds;
            this.render();
        });
    }

    destroy = () => {
        document.removeEventListener('categorieschanged', this.handleCategoriesChange);
    }

    handleCategoriesChange = async () => {
        this.feeds = await storage.getFeeds();
        const root = Category.fromFeedsObj(this.feeds);
        this.category = root.getScopedCategories(this.categoryTitle);
        this.render();

        console.log('rerendered');
    }

    handleCategoryEditClick = e => {
        router.navigator.pushPage('edit-category.html', {
            data: {
                path: this.categoryTitle,
                selectedFeeds: [],
            }
        });
    }

    render = () => {
        this.subCategoryItems = cloneWithoutChildren(this.subCategoryItems);
        for (var key in this.category.categories) {
            let sub = this.category.categories[key];
            let listItem = this.createSubCategoryListItem(sub);
            this.subCategoryItems.appendChild(listItem);
        }
        this.categoryFeeds = cloneWithoutChildren(this.categoryFeeds);
        for (let url in this.category.feeds) {
            let feed = this.feeds[url];
            let listItem = this.createFeedListItem(feed);
            this.categoryFeeds.appendChild(listItem);
        }
    }

    createSubCategoryListItem = (subCategory) => {
        var listItem = document.createElement('ons-list-item');
        listItem.setAttribute('tappable', true);
        listItem.setAttribute('modifier', 'chevron');

        var left = document.createElement('div');
        left.setAttribute('class', 'left');
        listItem.appendChild(left);

        var onsIcon = document.createElement('ons-icon');
        onsIcon.setAttribute('icon', 'md-collection-item-2');
        left.appendChild(onsIcon);

        var center = document.createElement('div');
        center.setAttribute('class', 'center');
        listItem.appendChild(center);

        var title = document.createElement('span');
        title.setAttribute('class', 'list-item__title overflow-ellipsis');
        title.innerText = subCategory.title;
        center.appendChild(title);

        var right = document.createElement('div');
        right.setAttribute('class', 'right');

        listItem.addEventListener('click', () => {
            router.navigator.pushPage('category.html', {
                data: {
                    category: this.categoryTitle.concat(subCategory.title),
                }
            });
        });

        listItem.appendChild(right);
        return listItem;
    }

    createFeedListItem = (feed) => {
        var siteUrlParts = feed.site.split('/');
        var favicon = siteUrlParts[0].replace('http:', 'https:') + '//' + siteUrlParts[2] + '/favicon.ico';

        var listItem = document.createElement('ons-list-item');
        listItem.setAttribute('tappable', true);
        listItem.setAttribute('modifier', 'chevron');

        var left = document.createElement('div');
        left.setAttribute('class', 'left');
        listItem.appendChild(left);

        var img = document.createElement('img');
        img.setAttribute('onerror', 'fn.handleerroriconnotfound.call(this)');
        img.setAttribute('src', favicon);
        img.setAttribute('class', 'list-item__thumbnail');
        left.appendChild(img);

        var center = document.createElement('div');
        center.setAttribute('class', 'center');
        listItem.appendChild(center);

        var title = document.createElement('span');
        title.setAttribute('class', 'list-item__title overflow-ellipsis');
        title.innerText = feed.title;
        center.appendChild(title);

        var right = document.createElement('div');
        right.setAttribute('class', 'right');

        listItem.addEventListener('click', () => {
            router.navigator.pushPage('specific-feed.html', {
                data: {
                    url: feed.url,
                }
            })
        });

        listItem.appendChild(right);
        return listItem;
    }
}

router.addPage(new PageRoute(
    'category.html',
    'category-page',
    pageElement => new CategoryPage(pageElement)));

export default CategoryPage;
