import notfoundimage from './images/notfound.png';
import iconnotfoundimage from './images/iconnotfound.png';
import copy from 'copy-to-clipboard';
import * as ons from 'onsenui';

const fn = window.fn = {};

fn['open'] = function () {
	var menu = document.getElementById('menu');
	menu.open();
};

fn['navigator'] = document.getElementById('navigator');

fn['load'] = function (page) {
	console.log('fn load', page);
	var content = document.getElementById('content');
	var menu = document.getElementById('menu');
	content.load(page)
		.then(menu.close.bind(menu));
};

fn['category'] = {};

fn['category']['load'] = function (page, data) {
	fn['category']['data'] = data;
	fn.load(page, data);
};

fn['category']['data'] = null;

fn['hasFocus'] = true;

fn['copyToClipboard'] = (msg, options) => {
	copy(msg, options);
	ons.notification.alert('Copied to clipboard', { cancelable: true, title: 'Success' });
};

function setFocus() { fn['hasFocus'] = true; }
function setBlurred() { fn['hasFocus'] = false; }

window.addEventListener('focus', setFocus);
window.addEventListener('blur', setBlurred);
document.addEventListener('resume', setFocus);
document.addEventListener('pause', setBlurred);

fn['handleerroriconnotfound'] = function () { this.setAttribute('src', iconnotfoundimage); }

fn['handlenotfound'] = function () { this.setAttribute('src', notfoundimage); }
